<template>
  <div class="main">
    <nav-bar v-bind:color="color.backgroundColor" v-bind:back="back.backgroundImage"></nav-bar>
    <div class="test">
      <div class="first" v-if="call" id="style">
        <div class="users">
          <div class="user1">
            <div class="image" v-bind:style="style">

            </div>
            <div class="text">
              <span style="font-weight: bold">{{ username }}</span>
            </div>
          </div>
          <div class="vs">
            <i class="fa fa-bolt"></i>
          </div>
          <div class="user2">
            <div class="image2" v-bind:style="style2">

            </div>
              <div class="text">
                {{ user2.username }}
              </div>
          </div>
        </div>
        <div class="btn">
          <v-btn
          @click="getTest"
          >
            Start Fight
          </v-btn>
        </div>
      </div>
      <div class="second" v-if="!call">
        <div class="question">
          <div class="question1" id="question1">
            <div class="words">
              <span>{{ real.question }}</span>
            </div>
            <div class="fill">
                <p>
                  <v-text-field
                  v-model="answer1"
                  label="Answer"
                  required
                  ></v-text-field>
                </p>
              <p class="btn">
                <v-btn
                small
                @click="check"
                >
                  Check
                </v-btn>
              </p>
            </div>
          </div>
            <div class="true" v-if="show">
              <div class="yes" id="yes">
                <span>Right</span>
              </div>
              <div class="no" id="no">
                <span>You are not right</span>
              </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
    import axios from 'axios'
    import navbar from './navbar'
    export default {
        name: "call",
        components:{
          'nav-bar':navbar
        },
        data(){
          return{
            username:localStorage.getItem('username'),
            style:{
              backgroundImage:`url(${localStorage.getItem('image')})`
            },
            style2:{
              backgroundImage:``,
            },
            color:{
              backgroundColor:'#59824e'
            },
            back:{
              backgroundImage:'url(https://images.unsplash.com/photo-1533022876396-2b6f9e38fbfc?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=e495e0fd33982a09987d9a3634724be7&auto=format&fit=crop&w=2134&q=80)'
            },
            user2:[],
            call:true,
            tests:[],
            real:[],
            real2:[],
            real3:[],
            real4:[],
            real5:[],
            all:[],
            answer1:'',
            wrong:'',
            answer4:'',
            show:true
          }
        },

        methods:{
          getTest(){
            this.call = false;
            axios.get(`http://localhost:8000/router/Test/`)
              .then(
                response =>{
                  this.tests = response.data;
                  var x = this.tests.length;
                  var rand;
                  var rand2;
                  var rand3;
                  var rand4;
                  var rand5;
                  function random() {
                    rand = Math.floor(Math.random() * x);
                    rand2 = Math.floor(Math.random() * x);
                    rand3 = Math.floor(Math.random() * x);
                    rand4 = Math.floor(Math.random() * x);
                    rand5 = Math.floor(Math.random() * x);
                    if(rand == rand2 || rand2 == rand3 || rand3 == rand4 || rand5 == rand || rand4 == rand2 || rand3 == rand5 || rand == rand3 || rand4 == rand5 || rand2 == rand5 || rand == rand4){
                      random()
                    }
                  }
                  random();
                  this.real = this.tests[rand];
                  this.real2 = this.tests[rand2];
                  this.real3 = this.tests[rand3];
                  this.real4 = this.tests[rand4];
                  this.real5 = this.tests[rand5];
                  // console.log(this.real2)
                  // console.log(this.real3)
                  // console.log(this.real4)
                  // console.log(this.real5)
                }
              )
              .catch(
                err =>{
                  console.log(err)
                }
              )
          },
          check(){
            document.getElementById('question1').style.display = "none";
            if(this.answer1 == this.real.answer || this.answer1.toLowerCase() == this.real.answer || this.answer1.toUpperCase() == this.real.answer || this.answer1 == this.real.answer.charAt(0).toLowerCase() || this.answer1 == this.real.answer.charAt(0).toUpperCase() ){
              setTimeout(function () {
                document.getElementById('yes').style.display = "block"
              },3000)
            }
            else{
              document.getElementById('no').style.display = "block";
              setTimeout(function () {
                document.getElementById('no').style.display = "none"
              },3000)
            }
          }
        },
        created(){
            axios.get('http://localhost:8000/router/profiles/')
              .then(
                response =>{
                  for(var x = 0;x < response.data.length;x++){
                    if(response.data[x].username == this.$route.params.username){
                      this.user2 = response.data[x]
                    }
                  }
                  this.style2.backgroundImage = `url(${this.user2.image})`
                }
              )
              .catch(
                err =>{
                  console.log(err)
                }
              )
          },
    }
</script>

<style scoped>

  .test{
    width: 40%;
    background-color: white;
    padding: 0px;
    margin: 0 auto;
    padding: 10px;
  }
  .btn{
    width: 100%;
    text-align: right;
  }
  .first{
  }
  .fill{
    width: 40%;
    margin: 0 auto;
  }
  .first{

  }
  .second{
    padding: 0;
  }
  .words{
    background-color: #59824e;
    color:white;
    padding: 10px;
  }
  .question{
    font-family: 'Raleway',sans-serif;
    font-size: 16px;
  }
  .vs{
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .vs i{
    font-size: 30px;
    color:#59824e
  }
  .image{
    width: 160px;
    height: 160px;
    background-position: center;
    background-size: cover;
    border-radius: 50%;
    margin: 0 auto;
    animation: left 0.6s ;
  }
  .image2{
    width: 160px;
    height: 160px;
    background-position: center;
    background-size: cover;
    position: relative;
    border-radius: 50%;
    animation: right 0.6s;
    margin: 0 auto;
  }
  .yes{
    font-size: 30px;
    font-family: 'Raleway',sans-serif;
    color:#59824e;
    animation:appear 0.5s;
    display: none;
  }
  .no{
    font-size: 30px;
    font-family: 'Raleway',sans-serif;
    color: #932c3a;
    display: none;
    animation:appear 0.5s;
  }
  @keyframes appear {
    from{
      position: relative;
      top:100px;
      opacity: 0;
    }
    to{
      position: relative;
      top:0;
      opacity: 1;
    }
  }
  .btn {
    width: 100%;
  }
  .btn button{
    font-family: 'Quicksand',sans-serif;
    background-color: #59824e;
    color:white;
  }
  .first{
    animation-duration: 0.4s;
  }
  @keyframes right {
    from{
      position: relative;
      right:200px;
      opacity: 0;
    }
    to{
      position: relative;
      right:0;
      opacity: 1;
    }
  }
  @keyframes disappear {
    from{
      position: relative;
      left:0px;
      opacity: 0;
    }
    to{
      position: relative;
      left:600px;
      opacity: 1;
    }
  }
  .users{
    display: flex;
  }
  .user1{
    width: 50%;
    padding: 20px;
  }
  .user2{
    width: 50%;
    padding: 20px;
  }
  .text{
    width: 100%;
    font-family: 'Quicksand',sans-serif;
    font-size: 16px;
    padding: 10px;
  }
  @keyframes left {
    from{
      position: relative;
      left:200px;
      opacity: 0;
    }
    to{
      position: relative;
      left:0;
      opacity: 1;
    }
  }
</style>



